import React, { Component } from 'react';
import './App.css';
import { data } from './LocalWordStore.js';
import 'semantic-ui-css/semantic.min.css';
import coatOfArms from './images/peru_coat_of_arms.png';
import WordCard from './WordCard';

class App extends Component {

  constructor(props) {
    super(props);
    this.state={
                searchInput: '',
                words: [] };
    this.handleSearchInput = this.handleSearchInput.bind(this);
    this.buildWordListComponents = this.buildWordListComponents.bind(this);
    this.performSearch = this.performSearch.bind(this);
  }

  componentDidMount() {
    this.setInitialState();
  }

  setInitialState() {
    this.setState({words: data});
  }

  performSearch(searchTerm) {
    if (searchTerm.length === 0) {
      this.setState({words: data});
      return;
    }

    const results = [];

    for (let i in data) {
      const quechua = data[i].quechua;
      const english = data[i].english;

      if (this.matchWord(searchTerm, quechua) || this.matchWord(searchTerm, english)) {
        results.push(data[i]);
      }
    }

    this.setState({words: results});
  }

  matchWord(searchTerm, word) {
    for(let i = 0; i < searchTerm.length; i++) {
      let a = searchTerm.charAt(i);
      let b = word.charAt(i);

      // If letter is non-esistent on either term, not a match
      if (!a || !b) {
        return false;
      }

      // If letter for each term differes, not a match
      if (a !== b) {
        return false;
      }
    }

    return true;
  }

  handleSearchInput(event) {
    const search = event.target.value;

    this.setState({searchInput: search}, () => { this.performSearch(search) });


  }

  buildWordListComponents(words) {
    if (words.length === 0) {
      return;
    }

    const wordCards = [];

    for (let i in words) {
      wordCards.push(<div key={words[i].quechua}><WordCard word={words[i]} /></div>);
    }


    return(<div>{ wordCards }</div>);
  }

  render() {
    return (
      <div id='app'>
        <nav id='nav-bar'>
          <div id='nav-bar-top'>
            <div id='nav-bar-top-inner'>
              <h1>Quechua Word Translator</h1>
            </div>
          </div>
          <div id='nav-bar-inner'>
            <img id='peru-coat-of-arms' src={ coatOfArms } alt='Peru coat of arms'/>
              <div id='search-bar-outer'>
                <div id='search-bar' className="ui fluid icon input">
                 <input id='search-input' 
                        type='text'
                        name='search'
                        placeholder='Enter search term...' 
                        value={this.state.searchTerm} 
                        onChange={this.handleSearchInput} />
                <i className="search icon"></i>
              </div>
            </div>  
          </div>
        </nav>    
        <main>    
          <div id='content-outer'>
            <div id='results-list' className='results-list'>
              { this.buildWordListComponents(this.state.words) }
            </div>
          </div>
        </main>
        <footer>
          
        </footer>
      </div>
    );
  }
}

export default App;
